import React from "react";
import { CSSTransition } from "react-transition-group";

const TodoItem = ({ key, todo, checkDone, removeTodo }) => {
  return (
    <div
      className="todo-item"
      style={{
        backgroundColor: !todo.completed ? "#232d2d" : "#006d6d"
      }}
    >
      {todo.completed && (
        <div className="todo-item__remove" onClick={() => removeTodo(todo)}>
          &times;
        </div>
      )}
      <p
        className="todo-item__text"
        style={{
          textDecoration: todo.completed ? "line-through" : "none"
        }}
      >
        {todo.todo}
      </p>
      <div className="todo-item__done" onClick={() => checkDone(todo)}>
        &#10003;
      </div>
    </div>
  );
};

export default TodoItem;



