import React, { Component } from "react";
import TodoItem from "./TodoItem";
import { TransitionGroup, CSSTransition } from "react-transition-group";

class App extends Component {
  state = {
    id: 1,
    todo: "",
    todoList: [],
    completedList: [],
    showList: false,
    show: false,
    hide: false
  };

  onSubmit = e => {
    e.preventDefault();
    if (this.state.todo === "") return;
    console.log(this.state);
    const { todo, id } = this.state;
    const todoItem = {
      id,
      todo,
      completed: false
    };

    // const todoList = this.state.todoList.unshift(todoItem);
    const todoList = [todoItem, ...this.state.todoList];
    this.setState({ todoList, todo: "", id: this.state.id + 1, show: true });
  };

  showCompletedList = () => {
    this.setState({ showList: !this.state.showList });
  };

  checkDone = todo => {
    if (!todo.completed) {
      const todoList = this.state.todoList.filter(item => item.id != todo.id);
      todo.completed = true;
      const completedList = [todo, ...this.state.completedList];
      this.setState({ todoList, completedList });
    } else if (todo.completed) {
      const completedList = this.state.completedList.filter(
        item => item.id != todo.id
      );
      todo.completed = false;
      const todoList = [todo, ...this.state.todoList];
      this.setState({ todoList, completedList });
    }
  };

  removeTodo = todo => {
    const completedList = this.state.completedList.filter(
      item => item.id != todo.id
    );
    this.setState({ completedList, show: false, hide: true });
  };

  render() {
    const todos = this.state.todoList.map(todo => (
      <CSSTransition key={todo.id} timeout={300} classNames="fade">
        <TodoItem
          todo={todo}
          removeTodo={this.removeTodo}
          checkDone={this.checkDone}
          show={this.state.show}
          hide={this.state.hide}
        />
      </CSSTransition>
    ));
    const completedTodos = this.state.completedList.map(todo => (
      <CSSTransition key={todo.id} timeout={300} classNames="fade">
        <TodoItem
          todo={todo}
          removeTodo={this.removeTodo}
          checkDone={this.checkDone}
          show={this.state.show}
          hide={this.state.hide}
        />
      </CSSTransition>
    ));
    return (
      <div className="app-container">
        <h1 className="app-heading">Todo App</h1>
        <form onSubmit={this.onSubmit} action="submit" className="todo-form">
          <input
            autoFocus={true}
            type="text"
            className="todo-input"
            placeholder="Your Todo..."
            value={this.state.todo}
            onChange={e => this.setState({ todo: e.target.value })}
          />
          <button className="todo-btn">Add</button>
        </form>
        <TransitionGroup className="todo-list__container">
          {todos}
        </TransitionGroup>
        <div onClick={this.showCompletedList} className="completed-list">
          <h2 className="heading__secondary">
            Completed List
            {this.state.showList ? <span>&#9650;</span> : <span>&nabla;</span>}
          </h2>
        </div>
        {this.state.showList && (
          <TransitionGroup className="todo-list__container">
            {completedTodos}
          </TransitionGroup>
        )}
        <div className="footer">
          <p>
            This Todo application using react with animation by Sam @&nbsp;
            <a href="http://samderlust.com/">samderlust.com</a>
          </p>
        </div>
      </div>
    );
  }
}

export default App;

